.title {
    font-size: 36px;
    color: #fff;
    margin: 30px;
}


.user {
    display: flex;
    margin: 0 0 20px 20px;
}

.user__user {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.user__description {
    border: 1px solid #111;
    border-radius: 15px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    width: 500px;
    margin: 0px 0px 0px 20px;
}

.user__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.user__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

img.avatar {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    object-fit: cover;
}